<template>
    <div style="height: 100%">
        <van-pull-refresh v-model="expressloading" @refresh="onRefresh" style="height: 100%">
        <div class="expressInfoCard" v-if="!isNetworkError">
            <div class="expressListInFirstDiv" v-if="shunfengData.length>0">
                <div class="expressOrderNumber" v-if="shunfengData[0].mailno">运单号 {{shunfengData[0].mailno}}</div>
                <div class="expressOrderNumber" v-else>暂无订单号</div>
                <img class="copyIcon" src="../../assets/images/mall/express/icon_copy.png" @click.stop="copyBtn(shunfengData[0].mailno)">
            </div>
            <div class="expressListInSecondDiv">
                <div class="expressCompanyIcon" style="display: inline-block">
                    <img :src="companyImg" :onerror="defaultPhoto">
                </div>
                <div class="expressName-sec" style="display: inline-block">顺丰速运</div>
                <div class="phoneBtnDiv" style="display: inline-block">
                    <img class="phoneBtn" src="../../assets/images/mall/express/button_contact_cut.png"  @click="callShop(expressTel)">
                </div>
            </div>
        </div>
        <div class="expressInfoCard-sec" style="margin-top: 10px" v-if="!isNetworkError">
            <div>
                <div class="track-rcol">
                    <div class="track-list">
                        <ul>
                            <li v-for="(item,i) in expressStatusShowData" :style="{'margin-top':i==0?'0.1rem':'0'}">
                                <i class="node-icon">
                                    <img :src="item.imgUrl" />
                                </i>
                                <div class="rightInfo">
                                    <span class="expressStatusTitle" style="color: #333333" v-if="i ==0">{{item.expressStatusTitle}}</span>
                                    <span class="expressStatusTitle" style="color: #999999" v-else>{{item.expressStatusTitle}}</span>
                                    <span class="updateTime">{{item.updateTime}}</span>
                                    <div>
                                        <span class="expressStatusMsg" v-if="!item.hasPhone">{{item.expressStatusMsg}}</span>
                                        <div class="expressStatusMsg11" v-if="item.hasPhone">
                                            <span class="expressStatusMsg">{{item.remarktext1}}</span>
                                            <span class="phoneMsg" @click="callShop(phoneNum)" >{{phoneNum}}</span>
                                            <span class="expressStatusMsg">{{item.remarktext2}}</span>
                                        </div>
                                    </div>
                                </div>
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
        </div>
        <div style="padding-top:100px;height:300px" v-if="isNetworkError">
            <NetworkError></NetworkError>
        </div>
        </van-pull-refresh>
    </div>
</template>

<script>
    import NetworkError from '@/common/networkError/NetworkError';
    import {  getShunFengRouteInfo } from "../../services/expressTrajectory";
    import { Toast, Dialog } from "vant";
    export default {
        name: "expressMsgList",
        components:{
            NetworkError,
        },
        props:{
            subbillno: String
        },
        data(){
            return {
                isNetworkError:false,
                shunfengData:[],
                companyImg:'',
                expressStatusShowData:[],
                hasPhone:false,
                phoneNum:'',
                remarktext1:'',
                remarktext2:'',
                expressTel:'',
                defaultPhoto:'this.src="' + require('../../assets/images/default/default_picture.png') + '"',
                copyMailno:'',
                expressloading:false,
            }
        },
        mounted() {
            this.getShunFengRouteInfoService(this.subbillno);
        },
        methods: {
            copyBtn(copyNum) {
                cordova.plugins.clipboard.copy(this.copyMailno, function () {
                    Toast("复制成功");
                }, function () {
                    console.log("error");
                });
            },
            resolveShunfengData(sfData){
                let str = this.shunfengData[0].mailno.toString();
                this.copyMailno=str;
                var yushu = str.length % 3;
                if(yushu > 0){
                    this.shunfengData[0].mailno= str.match(/.{3}/g).join(' ') + " " + str.substr(-yushu, yushu);
                } else {
                    this.shunfengData[0].mailno = str.match(/.{3}/g).join(' ');
                }
                sfData.forEach((item,index)=>{
                    let para={
                        imgUrl:'',
                        expressStatusTitle:'',
                        expressStatusMsg:item.routeMessage,
                        updateTime:item.routeTime,
                        textColor : index===0 ? '#333333': '#999999',
                        hasPhone:false,
                    };
                    var myreg = /(1[3456789]\d{9})/;
                    if (myreg.test(item.routeMessage)) {
                        para.hasPhone=true;
                        let phoneNum=[];
                        phoneNum=item.routeMessage.match(/(1)\d{10}/g);
                        this.phoneNum=phoneNum[0];
                        let textCatch = [];
                        textCatch = item.routeMessage.split(this.phoneNum);
                        para.remarktext1 = textCatch[0];
                        para.remarktext2 = textCatch[1];

                    }

                    switch (item.expressCode) {
                        case "0":    //已发货
                            para.imgUrl =index===0 ? require("../../assets/images/mall/express/Ship.png"):require("../../assets/images/mall/express/Ship_white.png");
                            para.expressStatusTitle='已发货';
                            this.expressStatusShowData.push(para);
                            break;
                        case "1":    //已揽收
                            para.imgUrl =index===0 ? require("../../assets/images/mall/express/icon_Pickup_cut.png"):require("../../assets/images/mall/express/icon_Pickup_cut_white.png");
                            para.expressStatusTitle='已揽收';
                            this.expressStatusShowData.push(para);
                            break;
                        case "2":  //运输中
                            para.imgUrl =index===0 ? require("../../assets/images/mall/express/icon_Delivery_cut.png"):require("../../assets/images/mall/express/icon_Delivery_cut_white.png");
                            para.expressStatusTitle='运输中';
                            this.expressStatusShowData.push(para);
                            break;
                        case "3":    //派送中
                            para.imgUrl =index===0 ? require("../../assets/images/mall/express/Dispatching.png"):require("../../assets/images/mall/express/Dispatching_white.png");
                            para.expressStatusTitle='派送中';
                            this.expressStatusShowData.push(para);
                            break;
                        case "33":case "70":    //妥投异常
                            para.imgUrl =index===0 ? require("../../assets/images/mall/express/icon_abnormal_cut.png"):require("../../assets/images/mall/express/icon_abnormal_cut_white.png");
                            para.expressStatusTitle='妥投异常';
                            this.expressStatusShowData.push(para);
                            break;
                        case "4":    //已签收
                            para.imgUrl =index===0 ? require("../../assets/images/mall/express/icon_Signfor_cut.png"):require("../../assets/images/mall/express/icon_Signfor_cut_white.png");
                            para.expressStatusTitle='已签收';
                            this.expressStatusShowData.push(para);
                            break;
                        default :
                            break;
                    }
                })
            },
            // 联系商家
            callShop(phone) {
                //   window.location.href = "tel:" + phone;
                // },
                const platform = navigator.userAgent;
                const isiOS = !!platform.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);
                if (isiOS) {
                    window.location.href = "tel:" + phone;
                } else {
                    Dialog.confirm({
                        title:"拨打电话",
                        message: phone,
                    }).then(() => {
                        window.location.href = "tel:" + phone;
                    }).catch(() => {
                        // on cancel
                        Dialog.close;
                    });
                }
            },
            getShunFengRouteInfoService(subbillno) {
                let param = {
                    subBillNo: subbillno,
                };
                getShunFengRouteInfo(param).then(result =>{
                    this.expressloading=false;
                    this.isNetworkError=false;
                    this.expressStatusShowData=[];
                    if (result.resultCode==0){
                        if (result.data){
                            this.companyImg=result.data.picUrl;
                            this.expressTel=result.data.expressTel;
                            this.shunfengData = result.data.orderRouteInfoList;
                            this.resolveShunfengData(this.shunfengData);
                        }else {
                            this.isNetworkError=true;
                        }
                    }else {
                        console.log(result);
                        this.$toast("查询轨迹出现异常，请稍后重试");
                        this.isNetworkError=true;
                    }
                }).catch(error => {
                    this.expressloading=false;
                    this.isNetworkError=true;
                })
            },
            onRefresh(){
                this.getShunFengRouteInfoService(this.subbillno);
            },
        },

    }
</script>

<style scoped lang="scss">
    .expressListInFirstDiv{
        font-family: PingFangSC-Medium;
        font-size: 14px;
        color: #333333;
        letter-spacing: 0;
        line-height: 23px;
        height: 23px;
        padding-top: 19px;
    }
    .expressOrderNumber{
        display: inline;
        margin-left: 15px;
    }
    .copyIcon {
        width:13px;
        height: 13px;
        display: inline;
        margin-left: 10px;
    }
    .phoneBtn{
        width: 55px;
        height: 23px;
    }
    .expressListInSecondDiv{
        margin-top: 10px;
        height: 42px;
        width: 100%;
        line-height: 40px;
    }
    .expressCompanyIcon{
        padding-left: 15px;
        vertical-align: middle;
    }
    .expressCompanyIcon img{
        width: 39px;
        height: 39px;
    }
    .expressName-sec{
        font-family: PingFangSC-Medium;
        font-size: 14px;
        color: #333333;
        letter-spacing: 0;
        margin-left: 10px;
        vertical-align:middle;
    }
    .phoneBtnDiv{
        position: relative;
        height: 40px;
        margin-left: 180px;
        margin-right: 20px;
        vertical-align:middle;
    }
    .expressInfoCard{
        background: #FFFFFF;
        box-shadow: 0 0 20px 0 rgba(214,214,214,0.50);
        border-radius: 8px;
        border-radius: 8px;
        height: 104px;
    }
    .expressInfoCard-sec{
        background: #FFFFFF;
        box-shadow: 0 0 20px 0 rgba(214,214,214,0.50);
        border-radius: 8px;
        border-radius: 8px;
    }
    .track-rcol{}
    .track-list{
        position:relative;
    }
    ul li{
        list-style:none;
    }
    ul {
        padding-left:20px;
        padding-top: 10px;
    }
    .rightInfo{
        line-height: 20px;
        width: 315px;
    }
    .track-list li{
        position:relative;
        padding:0px 0 30px 18px;
        line-height:1rem;
        /*border-left:1px dashed  #EEEEEE;*/
        background-image: linear-gradient(to bottom , #EEEEEE 0%, #EEEEEE 50%, transparent 50%);
        background-size: 1px 8px;
        background-repeat: repeat-y;
        color: #999;
    }
    .track-list li .node-icon{
        position: absolute;
        left: -11px;
        top: -3px;
        border-radius: 0.25rem;
        background-color: white;
        width: 0.5rem;
        height: 0.5rem;
    }
    .node-icon img{
        width: 23px;
        height: 23px;
    }
    .expressStatusTitle{
        font-family: PingFangSC-Medium;
        font-size: 16px;
        letter-spacing: 0;
    }
    .updateTime{
        font-family: PingFangSC-Regular;
        font-size: 12px;
        color: #999999;
        letter-spacing: 0;
        margin-left: 10px;
    }
    .expressStatusMsg{
        font-family: PingFangSC-Regular;
        font-size: 12px;
        color: #999999;
        letter-spacing: 0;
    }
    .phoneMsg{
        font-family: PingFangSC-Regular;
        font-size: 12px;
        color: #4A90E2;
        letter-spacing: 0;
    }
</style>